* {
  background-color: aqua;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 16;
}
body{
  padding: 68px;
}
input{
  color:grey;
  font-size: 1,7cm;
  width: 100%;

}

.card-grid{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
.card{
 align-content: center;
 border: solid grey;
 border-radius: 6px ;
 margin-bottom: 10px;
 margin-right: 10px;
 overflow: hidden;
}
.card p{
  padding: 5px;
  text-align: center;
}
.card img {
  max-height: 170px;
}